<template>
  <span class="py-2">
    <router-link
      to="/"
      @click="
        dataCableStore.deselectCables(), landingSitesStore.deselectLandingSite()
      "
    >
      <i
        class="bi bi-arrow-return-left"
        aria-label="return to cable list"
        id="return-icon"
      ></i>
    </router-link>
  </span>
  <span id="title">
    <strong>{{ landingSite?.name }}</strong><br />
  </span>

  <div v-if="landingSite" class="landing-site-details">
    <div class="list-group-item">
      <span class="field-label">country</span>
      <div class="field-value">{{ landingSite.country }}</div>
    </div>

    <div 
      v-if="landingSite.CableSystem_X_Landingsites" 
      class="list-group-item"
    >
      <span class="field-label">cables</span>
      <div class="field-value">
        <div
          v-for="cable in landingSite.CableSystem_X_Landingsites"
          :key="cable.CableSystem.id"
          class="cable-item"
        >
          <a
            @click="
              landingSitesStore.deselectLandingSite(),
              dataCableStore.setSelectCable(cable.CableSystem.id)
            "
          >
            {{ cable.CableSystem.name }}
          </a>
        </div>
      </div>
    </div>

    <div 
      v-if="landingSite.CableSystem_X_Landingsites_t" 
      class="list-group-item"
    >
      <span class="field-label">connected landing sites</span>
      <div class="field-value">
        <template v-for="connectedSite in landingSite.CableSystem_X_Landingsites_t" :key="connectedSite">
          <div 
            v-if="!landingSitesStore.isLandingSiteSelectedById(connectedSite)"
            class="connected-site-item"
          >
            <a @click="landingSitesStore.setSelectLandingSiteById(connectedSite)">
              {{ connectedSite }}
            </a>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { useDataCablesStore } from "@/store/datacables";
import { useLandingSitesStore } from "@/store/landingSites";
import { onBeforeRouteUpdate, useRoute } from "vue-router";
import { useClientHandle } from "@urql/vue";
import { queryLandingSiteById } from "@/service/hasura";
import { ref } from "vue";

const landingSite = ref();
const dataCableStore = useDataCablesStore();
const landingSitesStore = useLandingSitesStore();

const handle = useClientHandle();
const loadLandingSite = async (id: string) => {
  const result = await handle.client
    .query(queryLandingSiteById, { id: id })
    .toPromise();
  if (result.error) {
    console.error(result.error);
    return;
  }
  const data = result.data.Landingsites[0];
  landingSite.value = data;

  await dataCableStore.initialize();
  dataCableStore.setManySelectedCables(
    data.CableSystem_X_Landingsites.map((cable: any) => cable.CableSystem.id)
  );
};

const route = useRoute();
const id = route.params.landingSiteId.toString();
loadLandingSite(id);

async function setLandingSite() {
  await dataCableStore.initialize();
  if (!landingSitesStore.isLandingSiteSelectedById(id)) {
    landingSitesStore.setSelectLandingSiteById(id);
  }
}
setLandingSite();

onBeforeRouteUpdate((to) => {
  loadLandingSite(to.params.landingSiteId.toString());
});
</script>

