// Function to clear sidebar vessel
function clearSidebarVessel() {
  if (sidebarMarkerLayerGroup.value) {
    // Clear all markers from the layer group
    sidebarMarkerLayerGroup.value.clearLayers();
  }

  // Update the vesselPositions array to exclude the sidebar-created vessel
