  if (selectedLandingSite.value?.geoData?.length > 0) {
    selectedCables.value = selectedLandingSite.value;
  }
});

watch(() => landingSiteStore.selectLandingSites, (selectedLandingSites) => {
  if (selectedLandingSites.length > 0) {
    selectedLandingSite.value = inputArray.find((str) => selectedLandingSites[0].id == str.id);
  } else {
    selectedLandingSite.value = null;
    selectedCables.value = [];
  }
});

// Watcher for selectedCables
watch(() => selectedCables.value, (newValue, oldValue) => {
  if (!newValue || newValue.length === 0) {
    clearSidebarVessel();
    emit('update:viewingVesselTrack', false);
  }
});

async function getVesselLatestPosition(vesselId) {
  const vesselTrackingUrl = 'https://sdcs-api.dev.peasec.de/fishing/v3/events';
  const params = {
    limit: 1,
    offset: 0,
    sort: '-start',
    vessels: [vesselId]
  };
  
  try {
    // Try both loitering and port visit events
    params['datasets[0]'] = 'public-global-loitering-events:latest';
    const loiteringResponse = await axios.get(vesselTrackingUrl, { params });
    
    params['datasets[0]'] = 'public-global-port-visits-c2-events:latest';
    const portVisitResponse = await axios.get(vesselTrackingUrl, { params });
    
    let latestEvent = null;
    let eventType = '';
    
    // Get the latest event by comparing dates
    if (loiteringResponse.data.entries && loiteringResponse.data.entries.length > 0 &&
        portVisitResponse.data.entries && portVisitResponse.data.entries.length > 0) {
      // If we have both types of events, compare their dates
      const loiteringEvent = loiteringResponse.data.entries[0];
      const portVisitEvent = portVisitResponse.data.entries[0];
      
      const loiteringDate = new Date(loiteringEvent.end);
      const portVisitDate = new Date(portVisitEvent.end);
      
      if (loiteringDate > portVisitDate) {
        latestEvent = loiteringEvent;
        eventType = 'Loitering';
      } else {
        latestEvent = portVisitEvent;
        eventType = 'PortVisit';
      }
    } else if (loiteringResponse.data.entries && loiteringResponse.data.entries.length > 0) {
      // Only loitering events available
      latestEvent = loiteringResponse.data.entries[0];
      eventType = 'Loitering';
    } else if (portVisitResponse.data.entries && portVisitResponse.data.entries.length > 0) {
      // Only port visit events available
      latestEvent = portVisitResponse.data.entries[0];
      eventType = 'PortVisit';
    }
        
    if (latestEvent) {
      return {
        position: latestEvent.position,
        lastSeen: latestEvent.end,
        ssvid: latestEvent.vessel?.ssvid || 'Unknown',
        eventType: eventType,
        vessel: {
          ...latestEvent.vessel,
          name: latestEvent.vessel.name || 'Unknown Vessel'
        }
      };
    }
    
    return null;
  } catch (error) {
    console.error("Error fetching vessel position:", error);
    return null;
  }
}

async function initializeSearchBar() {
  await cableStore.initialize();
  
  // Create vessel options first
  const vesselSearchOptions = Object.entries(vesselIds).map(([_, vessel]) => {
    const flag = getCountryFlag(vessel.country);
    
    return {
      id: vessel.vesselId,
      name: vessel.shipname,
      type: 'vessel',
      ssvid: vessel.ssvid,
      color: '#636363',
      flag,
      country: vessel.country,
      vesselId: vessel.vesselId,
      shipname: vessel.shipname,
      ...vessel
    };
  });

  // Create other options
  const cableOptions = cableStore.getDataCables.map((cable) => ({
    ...cable,
    type: 'cable',
  }));

  const landingSiteOptions = landingSiteStore.getLandingSites.map((landingSite) => ({
    ...landingSite,
    type: 'landingSite',
  }));

  // Try putting vessels in second position since last position doesn't work
  inputArray = [...cableOptions, ...vesselSearchOptions, ...landingSiteOptions];
  landingSitesAndCables.value = inputArray;
}

function getCountryFlag(countryCode: string): string {
  if (!countryCode) return ''; // Default flag if no country code is provided

  // Handle specific cases for country codes
  const codeMapping: Record<string, string> = {
    'KOR': 'KR', // South Korea
    'SWE': 'SE', // Sweden
    'ARE': 'AE', // United Arab Emirates
    'ATG': 'AG', // Antigua and Barbuda
    'BRB': 'BB'  // Barbados
  };

  // Use mapped code or original code
  const normalizedCode = codeMapping[countryCode] || countryCode;

  try {
    // Ensure the code is exactly 2 characters long
    const twoLetterCode = normalizedCode.slice(0, 2).toUpperCase();
    
    // Convert the two-letter code to a flag emoji
    const codePoints = twoLetterCode
      .split('')
      .map(char => 127397 + char.charCodeAt());

    return String.fromCodePoint(...codePoints);
  } catch (error) {
    console.error(`Error converting country code ${countryCode} to flag:`, error);
    return ''; // Fallback flag if conversion fails
  }
}

onMounted(() => {
  if (!mapInstance.value) {
    console.error('mapInstance is null in TheSidebar.vue');
  }
});

