// find cable in list function
async function asyncFind(queryGiven: string) {
  clearSidebarVessel();

  if (!queryGiven) {
    landingSitesAndCables.value = inputArray;
    return;
  }

  const regex = new RegExp(queryGiven.replace(/[-[]{}()+?.,\^$|#\s]/g, '&'), 'i');
  
  // Filter maintaining the same order as initialization
  const filteredCables = inputArray.filter(item => 
    item.type === 'cable' && item.name && regex.test(item.name)
  );
  
  const filteredVessels = inputArray.filter(item => 
    item.type === 'vessel' && item.name && regex.test(item.name)
  );
  
  const filteredLandingSites = inputArray.filter(item => 
    item.type === 'landingSite' && item.name && regex.test(item.name)
  );

  landingSitesAndCables.value = [
    ...filteredCables,
    ...filteredVessels,
    ...filteredLandingSites
  ];
}

function selectCableOrLandingSiteOrVessel(selection) {
  if (selection.type === 'vessel') {
    // Set viewingVesselTrack to true when a vessel is selected
    emit('update:viewingVesselTrack', true);
    handleVesselSearchSelect(selection);
  } else if (selection.country) {
    cableStore.deselectCables();
    landingSiteStore.setSelectLandingSite(selection, true);
    // Reset viewingVesselTrack when non-vessel is selected
    emit('update:viewingVesselTrack', false);
  } else {
    landingSiteStore.deselectLandingSite();
    cableStore.setSelectCable(selection.id, true);
    // Reset viewingVesselTrack when non-vessel is selected
    emit('update:viewingVesselTrack', false);
  }
}

