<template>
  <span class="py-2">
    <router-link to="/" @click="dataCableStore.deselectCables()">
      <i class="bi bi-arrow-return-left" aria-label="return to cable list" id="return-icon"></i>
    </router-link>
  </span>
  
  <span id="title">
    <strong>{{cable?.name}}</strong><br/>
  </span>
  
  <div v-if="cable" class="cable-details">
    <div class="list-group-item">
      <span class="field-label">length</span>
      <div class="field-value">{{cable.length}}km</div>
    </div>
    
    <div class="list-group-item">
      <span class="field-label">operational</span>
      <div class="field-value">
        <i :class="['bi', !cable.outOfService ? 'bi-check-circle-fill text-success' : 'bi-x-circle-fill text-danger']"></i>
      </div>
    </div>
    
    <div class="list-group-item">
      <span class="field-label">ready for service</span>
      <div class="field-value">{{formatDate(cable.readyForService)}}</div>
    </div>
    
    <div v-if="cable.CableSystem_X_Landingsites" class="list-group-item">
      <span class="field-label">landing sites</span>
      <div class="field-value">
        <div v-for="point in cable.CableSystem_X_Landingsites" :key="point.Landingsite.id" class="landing-site-item">
          <a @click="landingSitesStore.setSelectLandingSiteById(point.Landingsite.id)">
            {{point.Landingsite.name}}
          </a>
        </div>
      </div>
    </div>
    
    <div v-if="cable.Ownerships" class="list-group-item">
      <span class="field-label">ownerships</span>
      <div class="field-value">
        <div v-for="entry in cable.Ownerships" :key="entry.Company.id" class="ownership-item">
          {{entry.Company.name}}{{entry.Company.nationality}}
        </div>
      </div>
    </div>
    
    <div class="list-group-item depth-analysis-section">
    <span class="field-label">Ocean Depth Analysis</span>
    <div class="field-value">
      <!-- Depth Analysis Controls -->
      <div class="depth-analysis-controls">
        <!-- Dropdown for sample point count -->
        <div class="control-row">
          <label for="samplePointCount" class="dropdown-label">Sample Points:</label>
          <select id="samplePointCount" v-model="samplePointCount" class="dropdown">
            <option v-for="count in [100, 200, 500, 1000, 2000, 5000]" :key="count" :value="count">
              {{count}}
            </option>
          </select>
        </div>
      
      <!-- Depth Analysis Button -->
      <div class="button-container">
        <button @click="extractDepthInfo" class="btn btn-depth-analysis" :disabled="isAnalyzing">
          <span v-if="!isAnalyzing">Extract Depth Info</span>
           <span v-else>Analyzing...</span>
        </button>
      </div>
    </div>

    <!-- Show depth analysis results if available -->
    <div v-if="filteredDepthAnalysis" class="depth-analysis-results">
      <div class="analysis-detail">
        <strong>Average Depth:</strong> {{filteredDepthAnalysis.averageDepth}}m
      </div>
      <div class="analysis-detail">
        <strong>Deepest Point:</strong> {{filteredDepthAnalysis.deepestPoint}}m
      </div>
    </div>
  </div>
  
  <OceanDepthAnalyzer 
    v-if="drawnGeometry"
    ref="oceanDepthAnalyzerRef"
    :mapInstance="mapInstance"
    :drawnGeometry="drawnGeometry"
    :samplePointCount="samplePointCount"
    @depth-analysis-complete="filterDepthAnalysis"
  />
</div>

  </div>

  <ElevationProfile 
    :mapInstance="mapInstance" 
    :geoData="geoData"
  />
</template>


<script setup lang="ts">
import { useDataCablesStore } from "@/store/datacables";
import { useLandingSitesStore } from "@/store/landingSites";
import { onBeforeRouteUpdate, useRoute } from "vue-router";
import { useClientHandle } from "@urql/vue";
import { queryCableById } from "@/service/hasura";
import { ref, watch} from "vue";
import OceanDepthAnalyzer from "./OceanDepthAnalyzer.vue";
import ElevationProfile from './ElevationProfile.vue';
import L from 'leaflet';

const dataCableStore = useDataCablesStore();
const landingSitesStore = useLandingSitesStore();
const route = useRoute();

const geoData = ref(null);
const cable = ref();
const handle = useClientHandle();
const drawnGeometry = ref(null);
const mapInstance = dataCableStore.getMapInstance;
const isAnalyzing = ref(false);
const oceanDepthAnalyzerRef = ref(null);
const filteredDepthAnalysis = ref(null);
const samplePointCount = ref(100);

const loadCable = async (id: string) => {
  const result = await handle.client.query(queryCableById, { id: id }).toPromise();
  if (result.error) {
    console.error(result.error);
    return;
  }
  cable.value = result.data.CableSystems[0];
};

function formatDate(dateString: string): string {
  return new Date(dateString).toLocaleDateString('en-US', {
    year: 'numeric', 
    month: 'long', 
    day: 'numeric'
  });
}

watch(() => dataCableStore.selectCable, () => {
  geoData.value = dataCableStore.cableDetails.geoData;
})
// Reset depth analysis when cable changes
watch(() => cable.value, () => {
  drawnGeometry.value = null;
  filteredDepthAnalysis.value = null;
  isAnalyzing.value = false;
  geoData.value = dataCableStore.cableDetails.geoData;
});

const extractDepthInfo = async () => {
  let cableGeo = dataCableStore.cableDetails;
  if (!cableGeo || !cableGeo.geoData) return;
  
  isAnalyzing.value = true;
  try {
    const flattenedCoordinates = cableGeo.geoData.flatMap(segment => 
      segment[0].map(coord => [coord[1], coord[0]])
    );

    const geoData = {
      type: 'LineString',
      coordinates: flattenedCoordinates
    };

     const latlngs = geoData.coordinates.map((coord: number[]) => 
      L.latLng(coord[1], coord[0])
    );
    const geometry = L.polyline(latlngs);

    drawnGeometry.value = geometry;
  } catch (error) {
    console.error('Error extracting depth info:', error);
  } finally {
    isAnalyzing.value = false;
  }
};

const filterDepthAnalysis = (fullAnalysis) => {
  const filteredAnalysis = {
    averageDepth: fullAnalysis.averageDepth,
    deepestPoint: fullAnalysis.deepestPoint,
    samplePoints: fullAnalysis.samplePoints
  };

  filteredDepthAnalysis.value = filteredAnalysis;
  console.log('Filtered depth analysis:', filteredAnalysis);
};

let id = route.params.cableId.toString();

if (!dataCableStore.isCableSelected(id)) {
  dataCableStore.setSelectCable(id, true);
}

loadCable(id);

onBeforeRouteUpdate((to) => {
  loadCable(to.params.cableId.toString());
});
</script>

